/* Posts and pages
--------------------------------------------- */
.site-content {
  margin: 0 0 40px; }

.home .site-content {
  padding-top: 30px; }

@media screen and (min-width: 992px) {
  .site-content {
    padding: 35px 0; } }

@media (min-width: 1200px) {
  .container-fluid.full-width-container {
    padding-left: 4.6875% !important;
    padding-right: 4.6875% !important; } }

#primary.site-main:not(.full-width-content), aside#secondary.widget-area:not(.full-width-content) {
  margin: 0 auto;
  max-width: 740px; }

article.sticky .thumbnail-container {
  position: relative;
  display: inline-block; }

article.sticky .thumbnail-container::before, article.sticky .thumbnail-container::after {
  content: "";
  padding: 9px 9px;
  display: inline-block;
  min-width: 18px;
  min-height: 18px;
  position: absolute;
  left: 1rem;
  top: 0.5rem;
  left: 2rem;
  min-width: 46px;
  min-height: 40px;
  padding: 7px 11px; }

article.sticky .thumbnail-container::before {
  z-index: 2;
  background-repeat: no-repeat;
  background-size: 1.5rem;
  background-position: center;
  filter: invert(1);
  background-image: url("data:image/svg+xml;base64,"); }

article.sticky .thumbnail-container::after {
  z-index: 1;
  background: #000000;
  box-shadow: -25px 20px 0 #000000; }

@media (max-width: 992px) {
  figure.thumbnail-post-single {
    border: 0 !important;
    margin-right: -15px !important;
    margin-left: -15px !important;
    padding: 0 !important; }
  .home.blog .thumbnail-container, .archive.category .thumbnail-container {
    margin-right: -15px !important;
    margin-left: -15px !important; }
    .home.blog .thumbnail-container a, .archive.category .thumbnail-container a {
      padding: 0 !important;
      width: 100% !important; } }

.post,
.page:not(body) {
  margin: 0 0 4em; }

header.entry-header {
  padding: 0 0 2rem; }

article {
  word-wrap: break-word; }
  article .entry-meta {
    padding-bottom: 1.25em; }
    article .entry-meta a {
      color: #555; }

.updated:not(.published) {
  display: none; }

.page-content,
.entry-summary {
  margin: 1.5em 0 0; }

.entry-content {
  padding: 3rem 0 4rem; }
  @media (max-width: 575px) {
    .entry-content {
      padding: 2.5rem 0 3rem; } }
  .entry-content form {
    margin-bottom: 40px; }

.entry-summary {
  padding-bottom: 4rem; }
  @media (max-width: 575px) {
    .entry-summary {
      padding-bottom: 3rem; } }

